header {
    padding-top: 6rem;
}

.home {
    /* background: rgb(227, 49, 223);
    background: linear-gradient(330deg, rgba(227, 49, 223, 1) 0%, rgba(82, 9, 103, 1) 100%); */
    width: 100%;
    border-radius: 1.5rem 1.5rem 8rem;
    display: flex;
    height: 33rem;
    width: 95%;
}

.home .banner-img {
    filter: drop-shadow(5px 0px 15px var(--color-white));
    position: absolute;
    height: 500px;
}

.home .content {
    background: var(--color-white);
    display: grid;
    place-content: center;
    padding: 0 10rem;
    margin-left: 5rem;
    margin-left: 13rem;
    border-radius: 3rem 3rem 10rem;
    box-shadow: 0 0 30px var(--color-primary);
}

.home .content h1 {
    font-weight: 600;
}

.home .content h3 {
    margin-bottom: 1rem;
}

.home .content h5 {
    margin-bottom: 1rem;
}

@media screen and (max-width:1048px) {
    .home {
        height: 30rem;
    }

    .home .banner-img {
        height: 400px;
    }

    .home .content {
        margin-left: 10.4rem;
    }
}

@media screen and (max-width:1024px) {
    .home {
        width: 100%;
    }

    .home .content {
        padding: 0 5rem;
    }
}

@media screen and (max-width:950px) {
    .home {
        align-items: center;
    }

    .home .banner-img {
        height: 16rem;
    }

    .home .content {
        padding: 3.5rem;
        height: fit-content;
        margin-left: 6.65rem;
    }
}

@media screen and (max-width:820px) {
    .home .content {
        border-radius: 2rem 2rem 6.8rem;
        padding: 2.8rem 3.5rem;
        margin-left: 6.23rem;
    }

    .home .banner-img {
        height: 15rem;
    }
}

@media screen and (max-width:750px) {
    .home .content {
        padding: 3rem;
    }

    .home .content h1 {
        font-size: 2rem;
    }

    .home .content h3:first-child {
        margin-bottom: 0;
    }

    .home .content h3:nth-child(3) {
        margin-bottom: 0.5rem;
    }
}

@media screen and (max-width:650px) {
    header {
        padding-top: 1rem;
    }

    .home {
        height: 25rem;
    }

    .home .banner-img {
        height: 10rem;
        top: 6rem;
    }

    .home .content {
        border-radius: 1.5rem 1.5rem 5.5rem;
        padding: 2.7rem;
        margin-left: 4.19rem;
    }

    .home .btn {
        padding: 0.5rem 1rem;
    }
}

@media screen and (max-width:550px) {
    .home .content {
        border-radius: 1.4rem 1.4rem 4rem;
        padding: 1.7rem 2.5rem;
        margin-left: 0;
        margin-top: 1rem;
    }

    .home .banner-img {
        height: 3rem;
        width: 3rem;
        border-radius: 50%;
        object-position: 6px top;
        background: var(--color-white);
        top: 1%;
        left: 1%;

    }
}

@media screen and (max-width:490px) {
    /* .home .banner-img {
        height: 7.5rem;
        top: 4rem;
    } */

    .home .content h1 {
        font-size: 1.8rem;
    }

    .home .content h3 {
        font-size: 1.1rem;
    }
}

@media screen and (max-width:392px) {
    .home .content {
        padding: 1.2rem 2rem;
    }

    .home .content h5 {
        font-size: 12px;
    }
}

