.NavBar-lg {
    display: flex;
    align-items: center;
    justify-content: space-between;
    place-content: center;
    position: fixed;
    width: 100%;
    padding: 1rem 1.8rem;
    z-index: 3;
}

.nav-scroll {
    box-shadow: 0 0 5px var(--color-primary);
    background: var(--color-bg);
}

.NavBar-lg a {
    color: var(--color-primary);
}

.NavBar-lg .navbar-brand img {
    width: 2.5rem;
}

.NavBar-lg ul {
    display: flex;
    gap: 2rem;
}

.NavBar-lg ul li {
    padding: 5px 5px;
}

.NavBar-lg ul li:hover {
    font-weight: 600;
}

.NavBar-lg ul li.active {
    font-weight: 600;
}

.NavBar-lg ul li.active .underline {
    width: 80%;
    height: 0.5px;
    margin: auto;
    background: var(--color-variant);
    transition: var(--transition);
    animation: navUnderLine 0.3s ease-in-out 0s 1 normal forwards;
}

@keyframes navUnderLine {
    0% {
        transform: scale(0.5);
    }

    100% {
        transform: scale(1);
    }
}

/* ======== Navbar small screen ========= */
@media screen and (max-width :650px) {
    .NavBar-lg {
        display: none;
    }
}

.NavBar-sm {
    display: block;
    background: rgba(253, 212, 255, 0.458);
    /* background: rgba(156, 8, 158, 0.425); */
    width: max-content;
    padding: 0.5rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(5px);
    box-shadow: 0 4px 7px var(--color-primary);
}

.NavBar-sm a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    font-size: 1.15rem;
    color: var(--color-primary);
    transition: var(--transition);
}

.NavBar-sm a:hover {
    background: rgba(0, 0, 0, 0.3);
}

.NavBar-sm a.active {
    background: var(--color-primary);
    color: var(--color-white);
}