.contact-container {
    margin: 3rem 0;
    display: flex;
    place-content: center;
    gap: 5rem;
}

.contact__options {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}


.contact__option {
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    transition: var(--transition);
    box-shadow: inset 0 4px 10px 5px rgba(0, 0, 0, 0.25), 0 1px rgba(255, 255, 255, 0.08);
}

.contact__option:hover {
    background: var(--color-primary);
    color: var(--color-white);
}

.contact__option-icon {
    margin-top: 5px;
    font-size: 1.5rem;
}

.contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
    color: inherit;
}

.contact__option:hover a {
    font-weight: 600;
    font-size: 1rem;
    text-shadow: 0px 0px 10px var(--color-variant);
}

/* ========== Form ============ */
form {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}


input,
textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background-color: transparent;
    border: 2px solid var(--color-primary);
    resize: none;
    color: var(--color-primary);
}

form .btn {
    width: 100%;
}

/* =========== Media Queries (Small Devices) ========= */
@media screen and (max-width:670px) {
    .contact-container {
        gap: 2.5rem;
    }
}

@media screen and (max-width:592px) {
    .contact-container {
        display: grid;
    }
}