.about-container {
    display: flex;
    margin-top: 3rem;
}

.about-me img {
    width: 400px;
    filter: drop-shadow(0px 0px 10px var(--color-primary));
}

.about-content {
    padding-right: 10rem;
}

.about-content p {
    margin-top: 0.6rem;
    margin-bottom: 1.2rem;
    font-size: 14px;
}

@media screen and (max-width:1024px) {
    .about-content {
        padding: 0rem;
    }

    .about-me img {
        width: 300px;
    }
}

@media screen and (max-width:820px) {
    .about-me img {
        width: 220px;
    }

    .about-container {
        align-items: center;
    }
}

@media screen and (max-width:698px) {
    .about-container {
        margin-top: 1.5rem;
    }

    .about-me img {
        width: 10rem;
    }

    .about-container .btn {
        padding: 0.7rem 1rem
    }
}

@media screen and (max-width:650px) {
    .about-container {
        display: grid;
        width: 90%;
        margin: auto;
    }

    .about-me img {
        margin: 2rem auto;
        width: 25rem;
    }
}

@media screen and (max-width: 650px) {
    section#about {
        margin-top: 2rem;
    }

    .about-me img {
        width: 22rem;
    }
}

@media screen and (max-width:490px) {
    .about-me img {
        width: 17rem;
    }
}